import React from 'react';

/**
 *  
 */
const Tabs = ({ tabs, activeTab, onChange, className = '' }) => {
    return (
        <div className={`tabs ${className}`}>
            {tabs.map((tab) => {
                const IconElement = tab.icon;

                //   
                const renderIcon = () => {
                    if (!IconElement) return null;

                    //   JSX Element  (React.isValidElement )
                    if (React.isValidElement(IconElement)) {
                        return IconElement;
                    }

                    // (React Component)  ForwardRef  
                    // ForwardRef typeof 'object' $$typeof Symbol(react.forward_ref)
                    if (typeof IconElement === 'function' ||
                        (typeof IconElement === 'object' && IconElement.$$typeof)) {
                        return <IconElement size={16} style={{ marginRight: 6 }} />;
                    }

                    return null;
                };

                return (
                    <button
                        key={tab.id}
                        className={`tab ${activeTab === tab.id ? 'tab-active' : ''}`}
                        onClick={() => onChange(tab.id)}
                    >
                        {renderIcon()}
                        {tab.label}
                    </button>
                );
            })}
        </div>
    );
};

export default Tabs;
