import { isStaticDeployment, getDemoData } from '../data/staticData';

// API  URL ( )
const API_BASE = '/api';

/**
 * KOSIS () API
 * https://kosis.kr/openapi/
 */
export const fetchKOSISData = async (apiKey, dataType) => {
    //      
    if (isStaticDeployment()) {
        console.info('[ ] KOSIS API    ');
        const demoData = getDemoData();
        return dataType === 'hpi' ? demoData.hpi : demoData.macro;
    }

    const endpoints = {
        hpi: 'DT_1YL20841', // 
        cpi: 'DT_1J20503', // 
        population: 'DT_1B04005N', // 
    };

    const orgId = 'KOSIS';
    const tblId = endpoints[dataType] || endpoints.hpi;

    try {
        const response = await fetch(`${API_BASE}/kosis`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                apiKey,
                orgId,
                tblId,
                startPrdDe: '200001',
                endPrdDe: '202512',
                format: 'json',
            }),
        });

        if (!response.ok) {
            throw new Error(`KOSIS API : ${response.status}`);
        }

        const data = await response.json();
        return transformKOSISData(data, dataType);
    } catch (error) {
        console.error('KOSIS API Error:', error);
        // :   
        const demoData = getDemoData();
        return dataType === 'hpi' ? demoData.hpi : demoData.macro;
    }
};

/**
 *  ECOS API
 * https://ecos.bok.or.kr/api/
 */
export const fetchBOKData = async (apiKey, dataType) => {
    //      
    if (isStaticDeployment()) {
        console.info('[ ]  API    ');
        return getDemoData().macro;
    }

    const statCodes = {
        interest: '722Y001', // 
        gdp: '200Y001',      // GDP
        m2: '101Y003',       // M2 
        exchange: '731Y001', // 
    };

    const statCode = statCodes[dataType] || statCodes.interest;

    try {
        const response = await fetch(`${API_BASE}/bok`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                apiKey,
                statCode,
                startDate: '200001',
                endDate: '202512',
                cycle: 'M', // Monthly
            }),
        });

        if (!response.ok) {
            throw new Error(` API : ${response.status}`);
        }

        const data = await response.json();
        return transformBOKData(data, dataType);
    } catch (error) {
        console.error('BOK ECOS API Error:', error);
        // :   
        return getDemoData().macro;
    }
};

/**
 *  API
 * https://www.reb.or.kr/
 */
export const fetchREBData = async (apiKey, dataType) => {
    //      
    if (isStaticDeployment()) {
        console.info('[ ]  API    ');
        return getDemoData().regional;
    }

    const endpoints = {
        regional: 'getRealtradeRegion', //  
        price: 'getHousePriceIndex',    // 
        jeonse: 'getJeonsePriceIndex',  // 
    };

    try {
        const response = await fetch(`${API_BASE}/reb`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                apiKey,
                endpoint: endpoints[dataType] || endpoints.regional,
            }),
        });

        if (!response.ok) {
            throw new Error(` API : ${response.status}`);
        }

        const data = await response.json();
        return transformREBData(data, dataType);
    } catch (error) {
        console.error('REB API Error:', error);
        // :   
        return getDemoData().regional;
    }
};

/**
 *   API
 * https://www.data.go.kr/
 */
export const fetchMOLITData = async (apiKey, params) => {
    //       
    if (isStaticDeployment()) {
        console.info('[ ]  API    ');
        return generateSampleTransactions();
    }

    const { regionCode, dealYmd } = params;

    try {
        const response = await fetch(`${API_BASE}/molit`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                apiKey,
                LAWD_CD: regionCode,
                DEAL_YMD: dealYmd,
            }),
        });

        if (!response.ok) {
            throw new Error(` API : ${response.status}`);
        }

        const data = await response.json();
        return transformMOLITData(data);
    } catch (error) {
        console.error('MOLIT API Error:', error);
        // :   
        return generateSampleTransactions();
    }
};

/**
 * API  
 */
export const testConnection = async (service, apiKey) => {
    //       ( )
    if (isStaticDeployment()) {
        return {
            success: true,
            message: '   -    ',
        };
    }

    try {
        const response = await fetch(`${API_BASE}/test`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ service, apiKey }),
        });

        const result = await response.json();
        return {
            success: response.ok,
            message: result.message || (response.ok ? ' ' : ' '),
        };
    } catch (error) {
        return {
            success: false,
            message: `  : ${error.message}`,
        };
    }
};

// =====    =====

function transformKOSISData(rawData, dataType) {
    if (!rawData || !Array.isArray(rawData)) return null;

    // KOSIS     
    const data = [];

    rawData.forEach(item => {
        const year = parseInt(item.PRD_DE?.substring(0, 4) || '0');
        const month = parseInt(item.PRD_DE?.substring(4, 6) || '0');
        const value = parseFloat(item.DT) || 0;

        data.push({
            date: `${year}.${String(month).padStart(2, '0')}`,
            year,
            month,
            value,
        });
    });

    return data;
}

function transformBOKData(rawData, dataType) {
    if (!rawData?.StatisticSearch?.row) return null;

    const rows = rawData.StatisticSearch.row;
    const data = [];

    rows.forEach(item => {
        data.push({
            date: item.TIME || '',
            year: parseInt(item.TIME?.substring(0, 4) || '0'),
            value: parseFloat(item.DATA_VALUE) || 0,
        });
    });

    return data;
}

function transformREBData(rawData, dataType) {
    if (!rawData || !rawData.items) return null;

    return rawData.items.map(item => ({
        id: item.regionCode,
        name: item.regionName,
        currentIndex: parseFloat(item.priceIndex) || 100,
        changeRate: parseFloat(item.changeRate) || 0,
        transactionVolume: parseInt(item.transactionCount) || 0,
        avgPrice: parseInt(item.avgPrice) || 0,
    }));
}

function transformMOLITData(rawData) {
    if (!rawData?.response?.body?.items?.item) return [];

    const items = rawData.response.body.items.item;

    return (Array.isArray(items) ? items : [items]).map(item => ({
        apartmentName: item[''] || '',
        dong: item[''] || '',
        area: parseFloat(item['']) || 0,
        floor: parseInt(item['']) || 0,
        price: parseInt(item['']?.replace(/,/g, '')) || 0,
        dealYear: item[''] || '',
        dealMonth: item[''] || '',
        dealDay: item[''] || '',
        buildYear: item[''] || '',
    }));
}

/**
 *     ( )
 */
function generateSampleTransactions() {
    const apartments = [
        { name: ' ', dong: '', basePrice: 350000 },
        { name: '', dong: '', basePrice: 450000 },
        { name: '', dong: '', basePrice: 320000 },
        { name: '', dong: '', basePrice: 280000 },
        { name: '', dong: '', basePrice: 260000 },
        { name: '', dong: '', basePrice: 220000 },
        { name: ' ', dong: '', basePrice: 240000 },
        { name: '', dong: '', basePrice: 290000 },
        { name: '', dong: '', basePrice: 380000 },
        { name: '  ', dong: '', basePrice: 310000 },
    ];

    const areas = [59.95, 84.97, 114.82, 135.55, 164.81];

    return apartments.flatMap((apt, aptIdx) => {
        const count = 2 + Math.floor(Math.random() * 3);
        const transactions = [];

        for (let i = 0; i < count; i++) {
            const area = areas[Math.floor(Math.random() * areas.length)];
            const priceVariation = 0.9 + Math.random() * 0.2;
            const areaMultiplier = area / 84.97;

            transactions.push({
                apartmentName: apt.name,
                dong: apt.dong,
                area: area,
                floor: 5 + Math.floor(Math.random() * 30),
                price: Math.round(apt.basePrice * priceVariation * areaMultiplier),
                dealYear: '2025',
                dealMonth: '01',
                dealDay: String(1 + Math.floor(Math.random() * 7)).padStart(2, '0'),
                buildYear: String(2005 + Math.floor(Math.random() * 15)),
            });
        }

        return transactions;
    });
}
