/**
 *   - RSS    API 
 *   (GitHub Pages )  
 */

import { isStaticDeployment, DEMO_NEWS } from '../data/staticData';

// CORS   ( )
const CORS_PROXIES = [
    (url) => `https://api.allorigins.win/raw?url=${encodeURIComponent(url)}`,
    (url) => `https://corsproxy.io/?${encodeURIComponent(url)}`,
    (url) => `https://api.codetabs.com/v1/proxy?quest=${encodeURIComponent(url)}`,
];

//   
const NEWS_SOURCES = {
    naver: {
        name: ' ',
        rssUrl: 'https://news.google.com/rss/search?q=+site:naver.com&hl=ko&gl=KR&ceid=KR:ko',
    },
    hankyung: {
        name: '',
        rssUrl: 'https://news.google.com/rss/search?q=+site:hankyung.com&hl=ko&gl=KR&ceid=KR:ko',
    },
    mk: {
        name: '',
        rssUrl: 'https://news.google.com/rss/search?q=+site:mk.co.kr&hl=ko&gl=KR&ceid=KR:ko',
    },
    chosun: {
        name: '',
        rssUrl: 'https://news.google.com/rss/search?q=+site:chosun.com&hl=ko&gl=KR&ceid=KR:ko',
    },
};

/**
 * CORS   URL fetch
 */
async function fetchWithCorsProxy(url, timeout = 5000) {
    for (const getProxyUrl of CORS_PROXIES) {
        try {
            const proxyUrl = getProxyUrl(url);
            const controller = new AbortController();
            const timeoutId = setTimeout(() => controller.abort(), timeout);

            const response = await fetch(proxyUrl, {
                signal: controller.signal,
                headers: { 'Accept': 'application/rss+xml, application/xml, text/xml' }
            });
            clearTimeout(timeoutId);

            if (response.ok) {
                return await response.text();
            }
        } catch (error) {
            //   
            console.warn('  ,   :', error.message);
        }
    }
    throw new Error(' CORS  ');
}

/**
 * Google News RSS    
 */
export const fetchRealEstateNews = async (category = 'all') => {
    const queries = {
        all: '',
        market: '+',
        policy: '+',
        analysis: '+',
        development: '+',
    };

    const query = queries[category] || queries.all;
    const rssUrl = `https://news.google.com/rss/search?q=${encodeURIComponent(query)}&hl=ko&gl=KR&ceid=KR:ko`;

    try {
        //          CORS  
        if (isStaticDeployment()) {
            const xmlText = await fetchWithCorsProxy(rssUrl);
            return parseRSSFeed(xmlText);
        }

        //  : Vite  
        const response = await fetch(`/api/rss?url=${encodeURIComponent(rssUrl)}`);

        if (!response.ok) {
            throw new Error('RSS   ');
        }

        const xmlText = await response.text();
        return parseRSSFeed(xmlText);
    } catch (error) {
        console.error('  :', error);
        //  :   
        return getDemoNewsWithCategory(category);
    }
};

/**
 *     
 */
function getDemoNewsWithCategory(category) {
    if (category === 'all') {
        return DEMO_NEWS;
    }
    return DEMO_NEWS.filter(news => news.category === category);
}

/**
 * RSS XML 
 */
function parseRSSFeed(xmlText) {
    try {
        const parser = new DOMParser();
        const xml = parser.parseFromString(xmlText, 'text/xml');

        //   
        const parseError = xml.querySelector('parsererror');
        if (parseError) {
            console.error('RSS  :', parseError.textContent);
            return DEMO_NEWS;
        }

        const items = xml.querySelectorAll('item');

        if (items.length === 0) {
            console.warn('RSS   ,   ');
            return DEMO_NEWS;
        }

        const news = [];
        items.forEach((item, index) => {
            if (index >= 20) return; //  20

            const title = item.querySelector('title')?.textContent || '';
            const link = item.querySelector('link')?.textContent || '';
            const pubDate = item.querySelector('pubDate')?.textContent || '';
            const source = item.querySelector('source')?.textContent || '';
            const description = item.querySelector('description')?.textContent || '';

            // HTML  
            const cleanDescription = description.replace(/<[^>]*>/g, '').substring(0, 200);

            //    
            let formattedDate;
            try {
                formattedDate = new Date(pubDate).toISOString().split('T')[0];
            } catch {
                formattedDate = new Date().toISOString().split('T')[0];
            }

            news.push({
                id: index + 1,
                title: title.replace(' - ' + source, ''),
                summary: cleanDescription,
                source,
                date: formattedDate,
                link,
                category: categorizeNews(title),
                sentiment: analyzeSentiment(title),
            });
        });

        return news.length > 0 ? news : DEMO_NEWS;
    } catch (error) {
        console.error('RSS  :', error);
        return DEMO_NEWS;
    }
}

/**
 *    
 */
function categorizeNews(title) {
    const lowerTitle = title.toLowerCase();

    if (lowerTitle.includes('') || lowerTitle.includes('') || lowerTitle.includes('')) {
        return 'policy';
    }
    if (lowerTitle.includes('') || lowerTitle.includes('') || lowerTitle.includes('')) {
        return 'analysis';
    }
    if (lowerTitle.includes('') || lowerTitle.includes('') || lowerTitle.includes('gtx')) {
        return 'development';
    }
    return 'market';
}

/**
 *   
 */
function analyzeSentiment(title) {
    const positiveKeywords = ['', '', '', '', '', '', '', ''];
    const negativeKeywords = ['', '', '', '', '', '', '', ''];

    const lowerTitle = title.toLowerCase();

    for (const keyword of positiveKeywords) {
        if (lowerTitle.includes(keyword)) return 'positive';
    }
    for (const keyword of negativeKeywords) {
        if (lowerTitle.includes(keyword)) return 'negative';
    }
    return 'neutral';
}

/**
 *    (Web Scraping )
 */
export const fetchNaverRealEstateNews = async () => {
    //   API   
    //  Google News RSS 
    return fetchRealEstateNews('all');
};
