import { useState, useMemo, useEffect, useCallback } from 'react';
import {
    PieChart, Pie, Cell, LineChart, Line, AreaChart, Area,
    XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend
} from 'recharts';
import {
    Briefcase, Plus, Trash2, Edit2, Save, X, Home, Building2,
    TrendingUp, TrendingDown, DollarSign, MapPin, Calendar,
    PiggyBank, Target, RefreshCw, Download
} from 'lucide-react';
import { formatNumber, formatCurrency, formatPercent } from '../utils/formatters';

//   
function PortfolioManager() {
    const [properties, setProperties] = useState([]);
    const [isAdding, setIsAdding] = useState(false);
    const [editingId, setEditingId] = useState(null);
    const [formData, setFormData] = useState({
        name: '',
        address: '',
        type: 'apartment',
        purchasePrice: '',
        currentPrice: '',
        loanAmount: '',
        purchaseDate: '',
        area: '',
        notes: ''
    });

    // LocalStorage 
    useEffect(() => {
        const saved = localStorage.getItem('portfolio_properties');
        if (saved) {
            try {
                setProperties(JSON.parse(saved));
            } catch (e) {
                console.error('   :', e);
            }
        }
    }, []);

    // LocalStorage 
    useEffect(() => {
        if (properties.length > 0) {
            localStorage.setItem('portfolio_properties', JSON.stringify(properties));
        }
    }, [properties]);

    const handleInputChange = useCallback((field, value) => {
        setFormData(prev => ({ ...prev, [field]: value }));
    }, []);

    const resetForm = useCallback(() => {
        setFormData({
            name: '', address: '', type: 'apartment',
            purchasePrice: '', currentPrice: '', loanAmount: '',
            purchaseDate: '', area: '', notes: ''
        });
        setIsAdding(false);
        setEditingId(null);
    }, []);

    const handleSave = useCallback(() => {
        if (!formData.name || !formData.purchasePrice) {
            alert('  .');
            return;
        }

        const newProperty = {
            id: editingId || Date.now(),
            ...formData,
            purchasePrice: Number(formData.purchasePrice) || 0,
            currentPrice: Number(formData.currentPrice) || Number(formData.purchasePrice) || 0,
            loanAmount: Number(formData.loanAmount) || 0,
            area: Number(formData.area) || 0,
            createdAt: editingId ? undefined : new Date().toISOString(),
            updatedAt: new Date().toISOString()
        };

        if (editingId) {
            setProperties(prev => prev.map(p => p.id === editingId ? { ...p, ...newProperty } : p));
        } else {
            setProperties(prev => [...prev, newProperty]);
        }
        resetForm();
    }, [formData, editingId, resetForm]);

    const handleEdit = useCallback((property) => {
        setFormData({
            name: property.name,
            address: property.address,
            type: property.type,
            purchasePrice: property.purchasePrice,
            currentPrice: property.currentPrice,
            loanAmount: property.loanAmount,
            purchaseDate: property.purchaseDate,
            area: property.area,
            notes: property.notes || ''
        });
        setEditingId(property.id);
        setIsAdding(true);
    }, []);

    const handleDelete = useCallback((id) => {
        if (confirm(' ?')) {
            setProperties(prev => {
                const updated = prev.filter(p => p.id !== id);
                if (updated.length === 0) {
                    localStorage.removeItem('portfolio_properties');
                }
                return updated;
            });
        }
    }, []);

    //  
    const stats = useMemo(() => {
        const totalPurchase = properties.reduce((sum, p) => sum + (p.purchasePrice || 0), 0);
        const totalCurrent = properties.reduce((sum, p) => sum + (p.currentPrice || 0), 0);
        const totalLoan = properties.reduce((sum, p) => sum + (p.loanAmount || 0), 0);
        const totalEquity = totalCurrent - totalLoan;
        const totalGain = totalCurrent - totalPurchase;
        const returnRate = totalPurchase > 0 ? (totalGain / totalPurchase) * 100 : 0;

        return { totalPurchase, totalCurrent, totalLoan, totalEquity, totalGain, returnRate };
    }, [properties]);

    //  
    const typeDistribution = useMemo(() => {
        const types = { apartment: '', officetel: '', villa: '', land: '', commercial: '' };
        const dist = properties.reduce((acc, p) => {
            acc[p.type] = (acc[p.type] || 0) + (p.currentPrice || 0);
            return acc;
        }, {});
        return Object.entries(dist).map(([key, value]) => ({
            name: types[key] || key,
            value,
            color: { apartment: '#6366f1', officetel: '#8b5cf6', villa: '#ec4899', land: '#10b981', commercial: '#f59e0b' }[key]
        }));
    }, [properties]);

    //  
    const regionDistribution = useMemo(() => {
        const dist = properties.reduce((acc, p) => {
            const region = p.address?.split(' ')[0] || '';
            acc[region] = (acc[region] || 0) + (p.currentPrice || 0);
            return acc;
        }, {});
        const colors = ['#6366f1', '#8b5cf6', '#ec4899', '#10b981', '#f59e0b', '#ef4444'];
        return Object.entries(dist).map(([name, value], i) => ({
            name, value, color: colors[i % colors.length]
        }));
    }, [properties]);

    //    ()
    const valueHistory = useMemo(() => {
        if (properties.length === 0) return [];
        const months = [];
        const now = new Date();
        for (let i = 11; i >= 0; i--) {
            const date = new Date(now.getFullYear(), now.getMonth() - i, 1);
            const month = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}`;
            const factor = 1 + (Math.random() - 0.5) * 0.02 * (11 - i);
            months.push({
                month,
                value: Math.round(stats.totalCurrent * factor),
                equity: Math.round(stats.totalEquity * factor)
            });
        }
        return months;
    }, [properties, stats]);

    const propertyTypes = [
        { value: 'apartment', label: '' },
        { value: 'officetel', label: '' },
        { value: 'villa', label: '/' },
        { value: 'land', label: '' },
        { value: 'commercial', label: '/' }
    ];

    return (
        <div className="page-container">
            <div className="page-header">
                <div>
                    <h1 className="page-title">
                        <Briefcase size={28} />
                         
                    </h1>
                    <p className="page-subtitle">     </p>
                </div>
                <button
                    className="btn btn-primary"
                    onClick={() => setIsAdding(true)}
                    style={{ display: 'flex', alignItems: 'center', gap: 8 }}
                >
                    <Plus size={18} />
                     
                </button>
            </div>

            {/*  /  */}
            {isAdding && (
                <div className="modal-overlay" onClick={resetForm}>
                    <div className="modal-content" onClick={e => e.stopPropagation()} style={{ maxWidth: 600 }}>
                        <div className="modal-header">
                            <h2>{editingId ? ' ' : '  '}</h2>
                            <button className="btn-icon" onClick={resetForm}><X size={20} /></button>
                        </div>
                        <div className="modal-body" style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: 16 }}>
                            <div className="form-group" style={{ gridColumn: 'span 2' }}>
                                <label> *</label>
                                <input
                                    type="text"
                                    value={formData.name}
                                    onChange={e => handleInputChange('name', e.target.value)}
                                    placeholder=":  "
                                />
                            </div>
                            <div className="form-group" style={{ gridColumn: 'span 2' }}>
                                <label></label>
                                <input
                                    type="text"
                                    value={formData.address}
                                    onChange={e => handleInputChange('address', e.target.value)}
                                    placeholder=":   "
                                />
                            </div>
                            <div className="form-group">
                                <label></label>
                                <select value={formData.type} onChange={e => handleInputChange('type', e.target.value)}>
                                    {propertyTypes.map(t => (
                                        <option key={t.value} value={t.value}>{t.label}</option>
                                    ))}
                                </select>
                            </div>
                            <div className="form-group">
                                <label> ()</label>
                                <input
                                    type="number"
                                    value={formData.area}
                                    onChange={e => handleInputChange('area', e.target.value)}
                                    placeholder="84"
                                />
                            </div>
                            <div className="form-group">
                                <label> () *</label>
                                <input
                                    type="number"
                                    value={formData.purchasePrice}
                                    onChange={e => handleInputChange('purchasePrice', e.target.value)}
                                    placeholder="100000"
                                />
                            </div>
                            <div className="form-group">
                                <label>  ()</label>
                                <input
                                    type="number"
                                    value={formData.currentPrice}
                                    onChange={e => handleInputChange('currentPrice', e.target.value)}
                                    placeholder="120000"
                                />
                            </div>
                            <div className="form-group">
                                <label>  ()</label>
                                <input
                                    type="number"
                                    value={formData.loanAmount}
                                    onChange={e => handleInputChange('loanAmount', e.target.value)}
                                    placeholder="50000"
                                />
                            </div>
                            <div className="form-group">
                                <label></label>
                                <input
                                    type="date"
                                    value={formData.purchaseDate}
                                    onChange={e => handleInputChange('purchaseDate', e.target.value)}
                                />
                            </div>
                            <div className="form-group" style={{ gridColumn: 'span 2' }}>
                                <label></label>
                                <textarea
                                    value={formData.notes}
                                    onChange={e => handleInputChange('notes', e.target.value)}
                                    placeholder="  "
                                    rows={2}
                                />
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button className="btn btn-secondary" onClick={resetForm}></button>
                            <button className="btn btn-primary" onClick={handleSave}>
                                <Save size={16} /> 
                            </button>
                        </div>
                    </div>
                </div>
            )}

            {/*   */}
            <div className="stats-grid" style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))', gap: 16, marginBottom: 24 }}>
                <div className="stat-card glass-card">
                    <div className="stat-icon" style={{ background: 'linear-gradient(135deg, #6366f1, #8b5cf6)' }}>
                        <Home size={24} />
                    </div>
                    <div className="stat-content">
                        <span className="stat-label"> </span>
                        <span className="stat-value">{properties.length}</span>
                    </div>
                </div>
                <div className="stat-card glass-card">
                    <div className="stat-icon" style={{ background: 'linear-gradient(135deg, #10b981, #14b8a6)' }}>
                        <DollarSign size={24} />
                    </div>
                    <div className="stat-content">
                        <span className="stat-label"> </span>
                        <span className="stat-value">{formatCurrency(stats.totalCurrent)}</span>
                    </div>
                </div>
                <div className="stat-card glass-card">
                    <div className="stat-icon" style={{ background: 'linear-gradient(135deg, #f59e0b, #f97316)' }}>
                        <PiggyBank size={24} />
                    </div>
                    <div className="stat-content">
                        <span className="stat-label"> (-)</span>
                        <span className="stat-value">{formatCurrency(stats.totalEquity)}</span>
                    </div>
                </div>
                <div className="stat-card glass-card">
                    <div className="stat-icon" style={{ background: stats.totalGain >= 0 ? 'linear-gradient(135deg, #10b981, #34d399)' : 'linear-gradient(135deg, #ef4444, #f87171)' }}>
                        {stats.totalGain >= 0 ? <TrendingUp size={24} /> : <TrendingDown size={24} />}
                    </div>
                    <div className="stat-content">
                        <span className="stat-label"> </span>
                        <span className="stat-value" style={{ color: stats.totalGain >= 0 ? 'var(--color-success)' : 'var(--color-danger)' }}>
                            {formatCurrency(stats.totalGain)} ({formatPercent(stats.returnRate)})
                        </span>
                    </div>
                </div>
            </div>

            <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(400px, 1fr))', gap: 24, marginBottom: 24 }}>
                {/*    */}
                <div className="glass-card" style={{ padding: 24 }}>
                    <h3 style={{ marginBottom: 16, display: 'flex', alignItems: 'center', gap: 8 }}>
                        <TrendingUp size={20} />   
                    </h3>
                    <ResponsiveContainer width="100%" height={250}>
                        <AreaChart data={valueHistory}>
                            <defs>
                                <linearGradient id="valueGradient" x1="0" y1="0" x2="0" y2="1">
                                    <stop offset="5%" stopColor="#6366f1" stopOpacity={0.3} />
                                    <stop offset="95%" stopColor="#6366f1" stopOpacity={0} />
                                </linearGradient>
                            </defs>
                            <CartesianGrid strokeDasharray="3 3" stroke="var(--color-border)" />
                            <XAxis dataKey="month" tick={{ fontSize: 12 }} stroke="var(--color-text-secondary)" />
                            <YAxis tickFormatter={v => formatCompactValue(v)} tick={{ fontSize: 12 }} stroke="var(--color-text-secondary)" />
                            <Tooltip formatter={(v) => formatCurrency(v)} />
                            <Area type="monotone" dataKey="value" stroke="#6366f1" fill="url(#valueGradient)" name=" " />
                            <Area type="monotone" dataKey="equity" stroke="#10b981" fill="none" name="" />
                            <Legend />
                        </AreaChart>
                    </ResponsiveContainer>
                </div>

                {/*   */}
                <div className="glass-card" style={{ padding: 24 }}>
                    <h3 style={{ marginBottom: 16, display: 'flex', alignItems: 'center', gap: 8 }}>
                        <Building2 size={20} />  
                    </h3>
                    {typeDistribution.length > 0 ? (
                        <ResponsiveContainer width="100%" height={250}>
                            <PieChart>
                                <Pie
                                    data={typeDistribution}
                                    cx="50%"
                                    cy="50%"
                                    innerRadius={60}
                                    outerRadius={100}
                                    paddingAngle={2}
                                    dataKey="value"
                                    label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                                >
                                    {typeDistribution.map((entry, index) => (
                                        <Cell key={index} fill={entry.color} />
                                    ))}
                                </Pie>
                                <Tooltip formatter={v => formatCurrency(v)} />
                            </PieChart>
                        </ResponsiveContainer>
                    ) : (
                        <div style={{ height: 250, display: 'flex', alignItems: 'center', justifyContent: 'center', color: 'var(--color-text-secondary)' }}>
                             
                        </div>
                    )}
                </div>

                {/*   */}
                <div className="glass-card" style={{ padding: 24 }}>
                    <h3 style={{ marginBottom: 16, display: 'flex', alignItems: 'center', gap: 8 }}>
                        <MapPin size={20} />  
                    </h3>
                    {regionDistribution.length > 0 ? (
                        <ResponsiveContainer width="100%" height={250}>
                            <PieChart>
                                <Pie
                                    data={regionDistribution}
                                    cx="50%"
                                    cy="50%"
                                    innerRadius={60}
                                    outerRadius={100}
                                    paddingAngle={2}
                                    dataKey="value"
                                    label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                                >
                                    {regionDistribution.map((entry, index) => (
                                        <Cell key={index} fill={entry.color} />
                                    ))}
                                </Pie>
                                <Tooltip formatter={v => formatCurrency(v)} />
                            </PieChart>
                        </ResponsiveContainer>
                    ) : (
                        <div style={{ height: 250, display: 'flex', alignItems: 'center', justifyContent: 'center', color: 'var(--color-text-secondary)' }}>
                             
                        </div>
                    )}
                </div>
            </div>

            {/*   */}
            <div className="glass-card" style={{ padding: 24 }}>
                <h3 style={{ marginBottom: 16, display: 'flex', alignItems: 'center', gap: 8 }}>
                    <Briefcase size={20} />   
                </h3>
                {properties.length > 0 ? (
                    <div style={{ overflowX: 'auto' }}>
                        <table className="data-table" style={{ width: '100%', borderCollapse: 'collapse' }}>
                            <thead>
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th> </th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {properties.map(prop => {
                                    const gain = (prop.currentPrice || 0) - (prop.purchasePrice || 0);
                                    const rate = prop.purchasePrice > 0 ? (gain / prop.purchasePrice) * 100 : 0;
                                    return (
                                        <tr key={prop.id}>
                                            <td style={{ fontWeight: 600 }}>{prop.name}</td>
                                            <td>{propertyTypes.find(t => t.value === prop.type)?.label || prop.type}</td>
                                            <td style={{ maxWidth: 200, overflow: 'hidden', textOverflow: 'ellipsis' }}>{prop.address || '-'}</td>
                                            <td>{formatCurrency(prop.purchasePrice)}</td>
                                            <td>{formatCurrency(prop.currentPrice)}</td>
                                            <td>{formatCurrency(prop.loanAmount)}</td>
                                            <td style={{ color: gain >= 0 ? 'var(--color-success)' : 'var(--color-danger)', fontWeight: 600 }}>
                                                {formatPercent(rate)}
                                            </td>
                                            <td>
                                                <div style={{ display: 'flex', gap: 8 }}>
                                                    <button className="btn-icon" onClick={() => handleEdit(prop)} title="">
                                                        <Edit2 size={16} />
                                                    </button>
                                                    <button className="btn-icon" onClick={() => handleDelete(prop.id)} title="" style={{ color: 'var(--color-danger)' }}>
                                                        <Trash2 size={16} />
                                                    </button>
                                                </div>
                                            </td>
                                        </tr>
                                    );
                                })}
                            </tbody>
                        </table>
                    </div>
                ) : (
                    <div style={{ textAlign: 'center', padding: 48, color: 'var(--color-text-secondary)' }}>
                        <Briefcase size={48} style={{ marginBottom: 16, opacity: 0.5 }} />
                        <p>   .</p>
                        <button className="btn btn-primary" onClick={() => setIsAdding(true)} style={{ marginTop: 16 }}>
                            <Plus size={16} />   
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
}

function formatCompactValue(value) {
    if (value >= 100000) return `${(value / 10000).toFixed(0)}`;
    if (value >= 10000) return `${(value / 10000).toFixed(1)}`;
    return `${formatNumber(value)}`;
}

export default PortfolioManager;
